@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/*Sticky-footer -> height of page*/
.page {
  height: 100%;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #000000;
  background-color: #ffffff;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}

@media (min-width: 1320px) {
  .page__body--index {
    background-image: url("../img/index/index_bg_desktop.jpg"), linear-gradient(to right, #ffffff 50%, #64a83c 50%);
    background-repeat: no-repeat;
    background-size: 720px 694px, 100% 694px;
    background-position: right top;
    border: none;
  }
}
@media (min-width: 1320px) and (min-resolution: 2dppx) {
  .page__body--index {
    background-image: url("../img/index/index_bg_desktop@2x.jpg"), linear-gradient(to right, #ffffff 50%, #64a83c 50%);
  }
}

.wrapper {
  padding: 0 20px;
  margin: 0 auto;
  max-width: 1320px;
}
.wrapper--catalog {
  padding: 0;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0 30px;
  }
}
@media (min-width: 768px) {
  .wrapper--tablet-large {
    padding: 0 70px;
  }
}
@media (min-width: 1320px) {
  .wrapper {
    padding: 0 50px;
  }
}

@media (min-width: 1320px) {
  .wrapper--example {
    margin-left: 0;
  }
}

.page-header {
  max-width: 1320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-header {
    display: flex;
  }
}

.page-header__wrapper {
  display: grid;
  grid-template-columns: 1fr 101px 1fr;
  align-items: center;
  border-bottom: 1px solid #e6e6e6;
  padding-top: 10px;
  padding-bottom: 14px;
  padding-left: 20px;
  padding-right: 20px;
  background: transparent;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    grid-template-columns: 1fr;
    padding: 0 30px;
    padding-top: 20px;
    border: none;
  }
}
@media (min-width: 1320px) {
  .page-header__wrapper {
    padding: 0 50px;
    padding-top: 53px;
    max-width: 1320px;
  }
}

.page-header__logo {
  grid-column: 1/3;
  width: auto;
  display: grid;
  grid-template-columns: 1fr 101px;
  align-items: center;
}
@media (min-width: 768px) {
  .page-header__logo {
    grid-template-columns: auto auto;
    gap: 12px;
  }
}

.page-header__toggle {
  grid-column: 3/4;
  height: 40px;
  width: 40px;
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: right;
  margin-left: auto;
  border: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__toggle--opened {
  background-image: url("../img/header/menu_button_close.svg");
}

.page-header__toggle--closed {
  background-image: url("../img/header/menu_button_open.svg");
}

.page-header__toggle--nojs {
  display: none;
}

@media (min-width: 768px) {
  .page-header__navigation {
    margin-left: auto;
    padding-right: 30px;
    padding-top: 22px;
  }
}
@media (min-width: 1320px) {
  .page-header__navigation {
    padding-right: 50px;
    padding-top: 49px;
  }
}

.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}

.logo__logo-text {
  justify-self: center;
}
@media (min-width: 768px) {
  .logo__logo-text {
    margin-top: -5px;
  }
}
@media (min-width: 1320px) {
  .logo__logo-text {
    width: 118px;
    height: 22px;
  }
}

.logo__logo-text--footer {
  width: 128px;
  height: 24px;
}

@media (min-width: 768px) {
  .logo__logo-image {
    height: 50px;
    width: 60px;
  }
}
@media (min-width: 1320px) {
  .logo__logo-image {
    height: 60px;
    width: 71px;
  }
}

.navigation--opened {
  position: absolute;
  width: 100%;
  min-width: 320px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .navigation--opened {
    position: static;
    min-width: auto;
    width: auto;
  }
}

.navigation--closed {
  display: none;
}
@media (min-width: 768px) {
  .navigation--closed {
    display: flex;
  }
}

.navigation--nojs {
  position: static;
  display: block;
  width: 100%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .navigation--nojs {
    background: transparent;
    width: auto;
  }
}

.navigation__list {
  padding: 0;
  list-style: none;
  margin: 0;
}
@media (min-width: 768px) {
  .navigation__list {
    display: flex;
  }
}

.navigation__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .navigation__link {
    font-size: 18px;
    line-height: 24px;
    padding-bottom: 3px;
    font-weight: 400;
  }
  .navigation__link--current {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1320px) {
  .navigation__link {
    font-size: 20px;
    line-height: 30px;
  }
}
.navigation__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}

@media (min-width: 1320px) {
  .navigation__list--index .navigation__link {
    color: #ffffff;
  }
  .navigation__list--index .navigation__link--current {
    border-bottom: 2px solid #ffffff;
  }
  .navigation__list--index .navigation__link:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  .navigation__list--index .navigation__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.navigation__item {
  text-align: center;
  padding-top: 22px;
  padding-bottom: 22px;
  padding-left: 0;
  padding-right: 0;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .navigation__item {
    border: none;
  }
}

@media (min-width: 768px) {
  .navigation__item:not(:last-child) {
    margin-right: 36px;
  }
}

.page-main__program {
  padding-top: 27px;
}
@media (min-width: 768px) {
  .page-main__program {
    padding-top: 47px;
  }
}
@media (min-width: 1320px) {
  .page-main__program {
    padding-top: 18px;
    margin-bottom: 135px;
  }
}

.page-main__principles {
  margin-bottom: 49px;
}
@media (min-width: 1320px) {
  .page-main__principles {
    margin-bottom: 12px;
  }
}

.page-main__example {
  padding-top: 27px;
  padding-bottom: 18px;
}
@media (min-width: 768px) {
  .page-main__example {
    padding-top: 21px;
    padding-bottom: 57px;
  }
}
@media (min-width: 1320px) {
  .page-main__example {
    padding-top: 0;
    padding-bottom: 18px;
  }
}

.page-main__title {
  margin-top: 28px;
  margin-bottom: 37px;
}
@media (min-width: 768px) {
  .page-main__title {
    margin-top: 48px;
    margin-bottom: 53px;
  }
}
@media (min-width: 1320px) {
  .page-main__title {
    margin-top: 64px;
    margin-bottom: 54px;
  }
}

.page-main__description {
  max-width: 450px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  margin-bottom: 39px;
}
@media (min-width: 768px) {
  .page-main__description {
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 69px;
  }
}
@media (min-width: 1320px) {
  .page-main__description {
    margin-bottom: 67px;
  }
}

.page-main__catalog-index {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .page-main__catalog-index {
    margin-bottom: 45px;
  }
}
@media (min-width: 1320px) {
  .page-main__catalog-index {
    margin-bottom: 43px;
  }
}

.page-main__extra {
  padding-top: 23px;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .page-main__extra {
    padding-top: 68px;
    margin-bottom: 73px;
  }
}
@media (min-width: 1320px) {
  .page-main__extra {
    padding-top: 79px;
    margin-bottom: 92px;
  }
}

.button {
  display: block;
  background-color: #68b738;
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  text-align: center;
  border: none;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 20px;
  padding-right: 20px;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--small {
  font-size: 16px;
  line-height: 16px;
}

.button--grey {
  font-size: 16px;
  line-height: 20px;
  color: #444444;
  background-color: #f2f2f2;
}
.button--grey:hover {
  background-color: #ebebeb;
}
.button--grey:active {
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .button--grey {
    margin-top: 7px;
    margin-left: 13px;
    margin-right: 13px;
  }
}
@media (min-width: 1320px) {
  .button--grey {
    margin: 0;
  }
}

.program {
  display: grid;
  font-family: "Oswald", "Arial", sans-serif;
  background-image: url("../img/index/index_bg_mobile.jpg"), linear-gradient(to right, #64a83c 50%, #62a53a 50%);
  background-size: 320px 302px, 100% 303px;
  background-repeat: no-repeat;
  background-position: top, top left;
  color: #ffffff;
  text-align: center;
}
@media (min-resolution: 2dppx) {
  .program {
    background-image: url("../img/index/index_bg_mobile@2x.jpg"), linear-gradient(to right, #64a83c 50%, #62a53a 50%);
  }
}
@media (min-width: 768px) {
  .program {
    background-image: none;
    color: #000000;
  }
}
@media (min-width: 1320px) {
  .program {
    min-height: 500px;
  }
}

.program__wrapper {
  display: grid;
  grid-template-rows: min-content min-content min-content min-content;
  grid-template-columns: 1fr;
  width: 100%;
  min-height: 461px;
}
@media (min-width: 768px) {
  .program__wrapper {
    justify-content: start;
  }
}

.program__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  margin: 0 auto;
  margin-bottom: 12px;
  width: 250px;
}
@media (min-width: 768px) {
  .program__title {
    margin: 0;
    margin-bottom: 23px;
    grid-row: 1/2;
    grid-column: 1/-1;
    padding-left: 87px;
    width: 60%;
    font-size: 60px;
    line-height: 60px;
    text-align: start;
    max-width: 501px;
  }
}
@media (min-width: 1320px) {
  .program__title {
    margin-top: 82px;
    padding-left: 80px;
    margin-bottom: 20px;
  }
}

.program__slogan {
  text-transform: uppercase;
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .program__slogan {
    grid-row: 2/3;
    grid-column: 1/-1;
    padding-left: 87px;
    margin-bottom: 19px;
    width: 60%;
    font-size: 20px;
    line-height: 20px;
    text-align: start;
    max-width: 501px;
  }
}
@media (min-width: 1320px) {
  .program__slogan {
    padding-left: 80px;
    margin-bottom: 22px;
  }
}

.program__picture {
  width: 100%;
  justify-self: center;
  align-self: start;
  display: grid;
}
@media (min-width: 768px) {
  .program__picture {
    grid-row: 3/4;
    grid-column: 1/-1;
  }
}
@media (min-width: 1320px) {
  .program__picture {
    padding-left: 448px;
    grid-row: 1/-1;
    z-index: -1;
  }
}

.program__image {
  margin: 0 auto;
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .program__image {
    padding-left: 0;
    width: 709px;
    height: 609px;
    margin-bottom: 0;
  }
}
@media (min-width: 1320px) {
  .program__image {
    width: 552px;
    height: 499px;
    margin: 0;
  }
}

@media (min-width: 768px) {
  .program__button {
    padding-top: 18px;
    margin-top: 29px;
    margin-left: 89px;
    margin-bottom: auto;
    grid-row: 3/4;
    grid-column: 1/-1;
    min-width: 244px;
    max-width: 414px;
    margin-right: auto;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 1320px) {
  .program__button {
    margin-left: 80px;
  }
}

.catalog-index__program-type {
  padding: 0;
}

@media (min-width: 768px) {
  .program-type {
    margin: 0;
  }
}
@media (min-width: 1320px) {
  .program-type {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.program-type__item {
  display: grid;
  grid-template-columns: 70px 1fr;
  grid-template-areas: "icon title" "description description" "link link";
  row-gap: 22px;
  font-family: inherit;
  list-style: none;
  background-color: #f2f2f2;
  padding-left: 20px;
  padding-top: 21px;
  padding-right: 33px;
  padding-bottom: 22px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .program-type__item {
    grid-template-columns: 1fr 80px 1fr;
    grid-template-areas: "title title icon" "description description icon" "link link icon";
    row-gap: 24px;
    padding-left: 53px;
    padding-top: 0;
    padding-right: 28px;
    padding-bottom: 0;
    margin-bottom: 30px;
  }
}
@media (min-width: 1320px) {
  .program-type__item {
    grid-template-columns: 162px 1fr;
    grid-template-areas: "icon title" "description description" "link link";
    padding-top: 36px;
  }
}

.program-type__item::after {
  content: "";
  display: inline-block;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
  margin-left: 2px;
  grid-area: icon;
  align-self: center;
}
@media (min-width: 768px) {
  .program-type__item::after {
    justify-self: center;
    align-self: center;
    width: 200px;
    height: 200px;
    margin-top: 12px;
    margin-left: 5px;
  }
}
@media (min-width: 1320px) {
  .program-type__item::after {
    justify-self: start;
    width: 100px;
    height: 100px;
    margin-left: 0;
  }
}

.program-type__item::before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  grid-area: icon;
  z-index: 1;
  align-self: start;
  justify-self: start;
}
@media (min-width: 768px) {
  .program-type__item::before {
    justify-self: center;
    align-self: center;
  }
}
@media (min-width: 1320px) {
  .program-type__item::before {
    justify-self: start;
  }
}

.program-type__item--slim::before {
  width: 50px;
  height: 53px;
  background-image: url("../img/index/cat-slim.svg");
  margin-top: -5px;
  margin-left: 2px;
}
@media (min-width: 768px) {
  .program-type__item--slim::before {
    width: 140px;
    height: 194px;
    margin-top: -17px;
    margin-left: 5px;
  }
}
@media (min-width: 1320px) {
  .program-type__item--slim::before {
    width: 70px;
    height: 97px;
    margin-left: 15px;
    margin-top: -10px;
  }
}

.program-type__item--pro::before {
  width: 67px;
  height: 50px;
  margin-left: -8px;
  margin-top: 3px;
  background-image: url("../img/index/cat-pro.svg");
}
@media (min-width: 768px) {
  .program-type__item--pro::before {
    width: 268px;
    height: 112px;
    margin-left: 5px;
    margin-top: 9px;
  }
}
@media (min-width: 1320px) {
  .program-type__item--pro::before {
    width: 134px;
    height: 56px;
    margin-left: -19px;
  }
}

.program-type__title {
  grid-area: title;
  display: flex;
  align-self: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  margin: 0;
  word-break: break-word;
}
@media (min-width: 768px) {
  .program-type__title {
    padding-top: 77px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1320px) {
  .program-type__title {
    padding-top: 7px;
  }
}

.program-type__description {
  grid-area: description;
  display: flex;
  color: #444444;
  line-height: 18px;
  margin: 0;
}
@media (min-width: 768px) {
  .program-type__description {
    padding-right: 12px;
    min-height: 120px;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1320px) {
  .program-type__description {
    padding-right: 0;
    min-height: initial;
    padding-top: 13px;
  }
}

.program-type__catalog-link {
  grid-area: link;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: inherit;
  padding-top: 20px;
  border-top: 1px solid #d9d9d9;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .program-type__catalog-link {
    border: none;
    padding-top: 6px;
    padding-bottom: 55px;
    font-size: 20px;
    line-height: 30px;
  }
}
.program-type__catalog-link:active {
  opacity: 0.3;
}

.program-type__catalog-link::after {
  content: "";
  display: flex;
  width: 24px;
  height: 10px;
  background-image: url("../img/index/icon_arrow.svg");
  background-repeat: no-repeat;
  background-position: center right;
  margin-left: 15px;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .program-type__catalog-link::after {
    margin-left: 25px;
  }
}

.program-type__catalog-link:hover::after {
  width: 32px;
}

.principles__title {
  margin-bottom: 43px;
  margin-top: 0;
}
@media (min-width: 768px) {
  .principles__title {
    margin-bottom: 32px;
    margin-top: 0;
  }
}

.principles__list {
  padding: 0;
  counter-reset: principle_number;
}
@media (min-width: 768px) {
  .principles__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(247px, 1fr));
    column-gap: 60px;
    row-gap: 17px;
  }
}
@media (min-width: 1320px) {
  .principles__list {
    grid-template-columns: repeat(4, minmax(230px, 1fr));
    column-gap: 80px;
    margin: 0;
  }
}

.principles__item {
  list-style: none;
  display: flex;
  position: relative;
}
@media (min-width: 768px) {
  .principles__item {
    min-height: 280px;
    padding-right: 60px;
  }
}
@media (min-width: 1320px) {
  .principles__item {
    padding-right: 0;
  }
}

@media (min-width: 768px) {
  .principles__item::after {
    counter-increment: principle_number;
    content: counter(principle_number);
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
    position: absolute;
    z-index: -1;
    left: calc((95% - 80px) / 2);
    top: 0;
  }
}
@media (min-width: 768px) and (min-width: 1320px) {
  .principles__item::after {
    right: 0;
    left: auto;
  }
}

.principles__item:not(:last-child) {
  margin-bottom: 20px;
}
@media (min-width: 1320px) {
  .principles__item:not(:last-child) {
