.checkbox {
  position: relative;
}

.checkbox__label {
  color: #222222;
  display: grid;
  grid-template-columns: 30px 1fr;
  gap: 14px;
  align-items: center;
}
@media (min-width: 768px) {
  .checkbox__label {
    gap: 28px;
  }
}

.checkbox__input {
  position: absolute;
  width: 30px;
  height: 30px;
  margin: 0;
  opacity: 0;
}

.checkbox__label::before {
  content: "";
  width: 30px;
  height: 30px;
  border: 1px solid #dcdcdc;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}
@media (min-width: 768px) {
  .checkbox__label::before {
    width: 40px;
    height: 40px;
  }
}

.checkbox__label--radio::before {
  border-radius: 50%;
}

.checkbox__label--checkbox::before {
  align-self: start;
}

.checkbox__input:hover ~ .checkbox__label::before {
  border-color: #bababa;
}

.checkbox__input:focus ~ .checkbox__label::before {
  border: 2px solid #444444;
}

.checkbox__input:active ~ .checkbox__label {
  opacity: 0.3;
}

.checkbox__input:active ~ .checkbox__label::before {
  opacity: 0.3;
}

.checkbox__input:active ~ .checkbox__label::after {
  opacity: 0.3;
}

.checkbox__input[type=checkbox]:checked ~ .checkbox__label::before {
  background-image: url("../img/form/icon_checkbox.svg");
}

.checkbox__input[type=radio]:checked ~ .checkbox__label::after {
  content: "";
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
  position: absolute;
  left: 8px;
}
@media (min-width: 768px) {
  .checkbox__input[type=radio]:checked ~ .checkbox__label::after {
    width: 18px;
    height: 18px;
    left: 11px;
  }
}

